import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import glamorous from 'glamorous'

class ScreenNav extends Component {

  constructor(props) {
    super(props)
    this.state = {

    }
  }

  render() {
    
    const NavBar = glamorous.div({
      backgroundColor: 'black', 
      height: '100%', 
      display: 'flex', 
      alignItems: this.props.display ? 'flex-start' : 'center', 
      padding: this.props.display ? '' :  '0 15px'
    });

    const Ul = glamorous.ul({
      marginBottom: '0', 
      width: '100%',
    });

    const Li = glamorous.li({
      display: this.props.display || 'inline-flex', 
      padding: this.props.display ? '20px' : '0 15px', 
      cursor: 'pointer',
      marginBottom: '0',
    });

    return (
      <NavBar css={this.props.css}>
        {this.props.children}

        <Ul>
      <Li><Link to="/">Home</Link></Li>
      <Li><Link to="/screen1">Screen 1</Link></Li>
      <Li><Link to="/screen2">Screen 2</Link></Li>
      <Li><Link to="/screen3">Screen 3</Link></Li>
    </Ul>       
        
        
      </NavBar>
    )
  }

}

module.exports = ScreenNav



