import React, { Component } from 'react';
import glamorous from 'glamorous';
import {
  Link,
} from 'react-router-dom'




class Sidebar extends Component {
  render() {

    const MySidebar = glamorous.div({
      backgroundColor: 'gray',
      height: 'auto',
      width: '5em',
      transform: this.props.toggle === 'open' ? 'translateX(-100%)' : 'translateX(0)'
    });
    // console.log(props.location.pathname.indexOf('screen1'))

    return (
      <MySidebar className={`${this.props.columnSize} columns`} css={this.props.css}>
        {this.props.sidebarContent}
        <Link to={`${this.props.match.url}/screen1/ass`}>Link</Link>
        <Link to={`${this.props.match.url}/screen1/titties`}>Bro</Link>
        </MySidebar>
    );
  }
}

export default Sidebar;



