import React, { Component } from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch,
  Redirect 
} from 'react-router-dom'
import glamorous from "glamorous";
import Container from "../LayoutComponents/Container/Container";
import Row from "../LayoutComponents/Row/Row";
import HeaderFooter from "../Header/Header";
import Sidebar from "../Sidebar/Sidebar";
import ScreenNav from "../ScreenNav";
import SubScreen1 from "../../screens/screen1/children/subScreen1";
import SubScreen2 from "../../screens/screen1/children/subScreen2";
import FaBars from "react-icons/lib/fa/bars";
import "../../styles/skeleton.css";

const MyApp = glamorous.div({
  margin: "0",
  padding: "0",
  backgroundColor: "#fff",
  color: "#444",
  overflowY: "hidden"
});
const StyledScreen = glamorous.div({ margin: "0", padding: "0" });

class Layout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      screenIndex: 1,
      sidebarIs: "closed"
    };

    this.toggleSidebar = this.toggleSidebar.bind(this);
  }

  componentWillMount() {
    // this.eventEmitter = new EventEmitter();
  }


  toggleSidebar() {
    this.setState({
      sidebarIs: this.state.sidebarIs === "open" ? "closed" : "open"
    });
    console.log("sidebar is: ", this.state.sidebarIs);
  }

  render() {
    return (
      <MyApp>
        {/* <Container css={{ height: "100vh" }}> */}
          <Row>
            <HeaderFooter className="twelve columns" css={{ height: "5vh" }}>
              <ScreenNav
                screenIndex={this.state.screenIndex}
              >
                <FaBars
                  width={"1.5em"}
                  height={"1.5em"}
                  onClick={this.toggleSidebar}
                />
              </ScreenNav>
            </HeaderFooter>
          </Row>

          <Container>
            <Row
              css={{
                marginLeft: this.state.sidebarIs === "open" ? "-22%" : "0"
              }}
            >
              <Sidebar {...this.props}
                columnSize="three"
                toggle={this.state.sidebarIs}
                css={{ height: "92vh" }}
              >
              
              </Sidebar>
              <StyledScreen className="twelve columns">

                <Switch>
                  <Route exact path="/screen1/ass" component={SubScreen1} />
                  <Route exact path="/screen1/titties" component={SubScreen2} />
                  
                </Switch>

              </StyledScreen>
            </Row>
          </Container>
        {/* </Container> */}
        <Row>
          <HeaderFooter
            className="twelve columns"
            css={{
              position: "absolute",
              bottom: "0",
              width: "100%",
              height: "3vh"
            }}
          >
            <p>Collin made this</p>
          </HeaderFooter>
        </Row>
      </MyApp>
    );
  }
}

module.exports = Layout


