import React, { Component } from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Switch
} from 'react-router-dom'
import glamorous from "glamorous";
import Container from "./components/LayoutComponents/Container/Container";
import Row from "./components/LayoutComponents/Row/Row";
import Layout from "./components/Layout/Layout";

import { EventEmitter } from "events";
import Screen1 from "./screens/screen1/Screen1";
import screen1Subscreen1 from "./screens/screen1/children/subScreen1";
import screen1Subscreen2 from "./screens/screen1/children/subScreen2";
import screen1Subscreen3 from "./screens/screen1/children/subScreen3";
import Screen2 from "./screens/screen2/Screen2";
import Screen3 from "./screens/screen3/Screen3";
// import RouterConfig from "./routerConfig";
import "./styles/skeleton.css";


// first our route components
// const Main = () => <h2>Main</h2>

// const App = () => <App/>

const RScreen1 = ({ routes }) => (
  <Screen1>
    <ul>
      <li><Link to="/screen1/ass">Bus</Link></li>
      <li><Link to="/screen1/titties">Cart</Link></li>
      <li><Link to="/screen1/bitches">Cart</Link></li>
    </ul>

    {routes.map((route, i) => (
      <RouteWithSubRoutes key={i} {...route}/>
    ))}
  </Screen1>
)

const navLinks = () => (
  <ul>
  <li><Link to="/screen1/ass">Bus</Link></li>
  <li><Link to="/screen1/titties">Cart</Link></li>
  <li><Link to="/screen1/bitches">Cart</Link></li>
</ul>

)

const RLayout = ({ routes }) => (
  <Layout navLinks={navLinks}>

    {/* {routes.map((route, i) => (
      <RouteWithSubRoutes key={i} {...route}/>
    ))} */}

  </Layout>
)

const Bus = () => <h3>Bus</h3>
const Cart = () => <h3>Cart</h3>

////////////////////////////////////////////////////////////
// then our route config
export const routerConfig= [
  { path: '/bus',
    component: Bus
  },
  { path: '/screen1',
    component: RScreen1,
    routes: [
      { path: '/screen1/ass',
        component: screen1Subscreen1
      },
      { path: '/screen1/titties',
      component: screen1Subscreen2
      },
      { path: '/screen1/bitches',
      component: screen1Subscreen3
      }
    ]
  }
]

// wrap <Route> and use this everywhere instead, then when
// sub routes are added to any route it'll work
const RouteWithSubRoutes = (route) => (
  <Route path={route.path} render={props => (
    // pass the sub-routes down to keep nesting
    <route.component {...props} routes={route.routes}/>
  )}/>
)

ReactDOM.render(
  <Router>
     <Container css={{ height: "100vh" }}>

      <Switch>
        <Route path='/' render={props => <Layout {...props} />} />
      </Switch>
         
  {/* <div>
  <RLayout sidebarContent='this content right here'>
 


    {routerConfig.map((route, i) => (
      <RouteWithSubRoutes key={i} {...route}/>
    ))}
    </RLayout>
  </div> */}
  </Container>
</Router>,
  document.getElementById("root")
);



